.accordion-title {
	width: 100%;
	padding: 10px 0px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 12px;
	color: #1d1d1f;
	border-bottom: 1px solid rgba(0, 0, 0, 0.2);
	cursor: pointer;
}

.accordion-list {
	display: none;
}

.accordion.active .accordion-list {
	display: block;
	transition: all 5s ease-in;
}
.accordion.active .accordion-title {
	border-bottom: none;
}
.accordion.active .accordion-list li {
    cursor: pointer;
    padding: 8px 15px;
    margin: 0;
}
.accordion.active .accordion-list li:hover a {
	text-decoration: underline;
}

/* Plus Icon */
.plus-icon {
	width: 20px;
	height: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}
.plus-icon .plus-first-stroke {
	height: 10px;
	width: 2px;
	background-color: rgba(0, 0, 0, 0.5);
	position: absolute;
	transition: all 0.5s ease-in-out;
}
.plus-icon .plus-second-stroke {
	height: 2px;
	width: 10px;
	background-color: rgba(0, 0, 0, 0.5);
	position: absolute;
	transition: all 0.5s ease-in-out;
}
.plus-icon.active .plus-first-stroke {
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}
.plus-icon.active .plus-second-stroke {
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}

/* Menu Icon */
.menu-icon {
	width: 30px;
	height: 30px;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.menu-icon .menu-first-stroke {
	width: 18px;
	height: 1px;
	background-color: #d3d3dd;
	position: absolute;
	top: 11px;
	transition: all 0.5s ease-in-out;
}

.menu-icon .menu-second-stroke {
	width: 18px;
	height: 1px;
	background-color: #d3d3dd;
	position: absolute;
	bottom: 11px;
	transition: all 0.5s ease-in-out;
}
.menu-icon.active .menu-first-stroke {
	-webkit-transform: translateY(2px) rotate(45deg);
	        transform: translateY(2px) rotate(45deg);
}
.menu-icon.active .menu-second-stroke {
	-webkit-transform: translateY(-5px) rotate(-45deg);
	        transform: translateY(-5px) rotate(-45deg);
}

