		height: 100vh;
		-webkit-backdrop-filter: none;
		        backdrop-filter: none;
		display: block;
		background-color: rgba(0, 0, 0, 1);
		align-items: flex-start;
		justify-content: center;
		transition: height 2s;
		-webkit-animation: slide-down 2s;
		        animation: slide-down 2s;
		}
	nav.open .small-nav-items {
		margin-top: 5px;
	}
	nav .search-container,
	nav .toggle-nav-item {
		display: none;
	}
	nav .nav-items {
		display: none;
	}
	nav .nav-items li a .apple-icon {
		font-size: 20px;
	}
	nav.open .search-container {
		display: block;
		border-radius: 9px;
		width: 90%;
		height: 38px;
		margin: 15px auto;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 5px;
		color: #6e6e73;
		background-color: rgba(255, 255, 255, 0.1);
	}
	.search-container input {
		height: 29px;
		border: none;
		display: flex;
		flex: 1 1;
		margin-left: 3px;
		background-color: transparent;
		font-size: 18px;
	}
	nav.open .toggle-nav-item {
		display: block;
		border-top: 0.05px solid rgba(255, 255, 255, 0.5);
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-top: 20px;
	}
	nav .toggle-nav-item li {
		width: 90%;
		margin: 8px 0;
		padding-bottom: 10px;
		border-bottom: 0.05px solid rgba(255, 255, 255, 0.5);
	}
	nav .toggle-nav-item li a {
		font-size: 17px;
	}
	.small-nav-items {
		display: flex;
		width: 100%;
		align-items: center;
		justify-content: space-between;
	}
	.small-nav-items li .menu-icon-container {
		cursor: pointer;
	}
	.small-nav-items li .menu-icon-container:focus {
		background-color: none;
	}
}

